import Logo from "assets/imgs/logo.png";
import { useEffect, useState } from "react";

const Header = (props) => {
    const [headerStat, setHeaderStat] = useState(false);

    const mouseOver = () => {
        setTimeout(() => {
            setHeaderStat(true);
        }, 0);
    }

    const mouseOut = () => {
        setTimeout(() => {
            setHeaderStat(false);
        }, 0);
    }

    useEffect(() => {
        const target = document.querySelector(".header");

        target.addEventListener("mouseover", () => {
            mouseOver();
        });
        target.addEventListener("mouseleave", () => {
            mouseOut();
        });

        return () => {
            target.removeEventListener('mouseover', mouseOver);
            target.removeEventListener('mouseleave', mouseOver);
        }

    }, [])

    return (
        <div className="header">
            <div className="headerContainer1">
                <div className="headerLogo">
                    <img src={Logo} alt="logo" onClick={() => props.func(0)} />
                </div>
                <div className="headerMenu">
                    <ul>
                        <li className="subMenuToggle"></li>
                        {/* <li className="subMenuToggle"></li> */}
                        <li className="subMenuToggle"></li>
                        <li className="subMenuToggle"></li>
                    </ul>
                </div>
            </div>
            <div className={`headerContainer2 ${headerStat ? "vOn" : "vOff disnone"}`}>
                <div className="headerLogo">
                    &nbsp;
                </div>
                <div className="headerSubMenu">
                    <ul>
                        <li>
                            <ul>
                                <li onClick={() => props.func(1)}></li>
                                <li onClick={() => props.func(2)}></li>
                                <li onClick={() => props.func(3)}></li>
                            </ul>
                        </li>
                        {/* <li>
                            <ul>
                                <li onClick={() => props.func(4)}></li>
                            </ul>
                        </li> */}
                        <li>
                            <ul>
                                <li onClick={() => props.func(5)}></li>
                            </ul>
                        </li>
                        <li>
                            <ul>
                                <li onClick={() => props.func(6)}></li>
                                <li onClick={() => props.func(7)}></li>
                                <li onClick={() => props.func(8)}></li>
                                <li onClick={() => props.func(9)}>1:1 </li>
                                <li onClick={() => props.func(10)}></li>
                            </ul>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    )
}

export default Header;